<template>
  <div class="my-container">

    <!-- $store.state.user -->
    <div class="header user-login" v-if="$store.state.user">
      <div class="base-info">
        <div class="left">

          <!-- 头像设置 -->
          <van-image
            round
            class="avatar"
            :src="userInfo.photo"
          />

          <!-- 用户昵称 -->
          <span class="name">{{ userInfo.name }}</span>
        </div>
        <div class="right">
          <van-button
            round
            type="default"
            siza="mini"
            to="/user"
          >
            编辑资料
          </van-button>
        </div>
      </div>

      <!-- 个人数据 -- Grid宫格 -->
      <div  class="hobby-info">
        <van-grid>
          <van-grid-item text="头条">
            <template #icon>
              <div>{{ userInfo.art_count }}</div>
            </template>
          </van-grid-item>

          <van-grid-item icon="photo-o" text="关注">
            <template #icon>
              <div>{{ userInfo.follow_count}}</div>
            </template>
          </van-grid-item>

          <van-grid-item icon="photo-o" text="粉丝">
            <template #icon>
              <div> {{ userInfo.fans_count }}</div>
            </template>
          </van-grid-item>

          <van-grid-item icon="photo-o" text="获赞">
            <template #icon>
              <div> {{ userInfo.like_count }} </div>
            </template>
          </van-grid-item>
        </van-grid>
      </div>
    </div>

    <!-- 顶部登录/注册盒子 -->
    <div class="header not-login" v-else>
      <!-- 跳转登录界面 $router.push() 直接跳转-->
      <div class="login-btn" @click="$router.push('/login')">
        <img class="mobile-img" src="../../assets/mobile.png" alt="">
        <span class="text">登录/注册</span>
      </div>
    </div>

    <!-- 宫格导航 -->
    <!--
      01 一般属性里的值都是字符串
      02 如果希望传递的是一个固定的值, 但他不是字符串时, 需要借用动态属性的v-bind
     -->
    <div >
      <van-grid :column-num="2" class="gird-nav mb-10">
        <van-grid-item icon="photo-o" text="收藏">
          <template>
            <i slot='icon' class="toutiao toutiao-shoucang"></i>
          </template>
        </van-grid-item>
        <van-grid-item icon="photo-o" text="历史" >
          <template>
            <i slot='icon' class="toutiao toutiao-lishi"></i>
          </template>
        </van-grid-item>
      </van-grid>
    </div>

    <!-- 单元格导航 -->
    <van-cell title="消息通知" is-link />
    <van-cell title="小智同学" is-link class="mb-10" />
    <van-cell
      title="退出登录"
      class="logout-cell"
      v-if="$store.state.user"
      @click="loginOut()"
      clickable
    />
  </div>
</template>

<script>
// 通过内置 mapState方法, 获取vuex中的state全局数据 导入的数据必须写在计算属性(computed)中
import { mapState } from 'vuex'
import { getUserInfoAPI } from '../../api/index.js'

export default {
  name: 'myApp',
  data () {
    return {
      userInfo: {}
    }
  },

  methods: {
    async loadUserInfo () {
      try {
        const res = await getUserInfoAPI()
        // console.log(res.data.data)
        this.userInfo = res.data.data
      } catch (err) {
        this.$toast('获取数据失败')
      }
    },

    loginOut () {
      this.$dialog.confirm({
        title: '确认退出？'
      })
        .then(() => {
          // on confirm
          // 点击确认触发
          // 退出登录 -- 清空登录状态 -- 删除本地t和vuex中的user (设为空)
          // 使用$store.commit() 调用修改state属性的方法清空user全局属性
          this.$store.commit('setUser', null)
        })
        .catch(() => {
          // on cancel
          // 点击取消触发
        })
    }
  },

  computed: { // vuex 中的数据用在data里,数据更新时页面不会实时更新.所以需要用在计算属性里面
    // 生成与State对话的方法-- ...map(映射)通过解构赋值拿到vuex中State中的 user
    // 拿到后通过 $store.state.user 使用; $store 是 vuex 的全局对象
    ...mapState(['user'])
  },

  created () {
    // 如果用户登陆了, 请求用户信息
    if (this.user) {
      this.loadUserInfo()
    }
  }
}
</script>

<style lang="less" scoped>
  .my-container {
    .header {
      min-height: 360px;
      background: url('../../assets/banner.png');
      background-size: cover;
    }

    .base-info {
      height: 230px;
      padding: 76px 30px 24px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .left {
        display: flex;
        align-items: center;

        .avatar {
          width: 130px;
          height: 130px;
          border: 4px solid #fff;
          margin-right: 24px;
        }

        .name {
          font-size: 28px;
          color: #fff;
        }
      }
    }

    .not-login {
          display: flex;
          justify-content: center;
          align-items: center;

        .login-btn {
          display: flex;
          justify-content: center;
          align-items: center;
          // 改变flax主轴方向
          flex-direction: column;

        .mobile-img {
          width: 130px;
          height: 130px;
          margin-bottom: 15px;
        }

        .text{
          font-size: 28px;
          color: #fff;
        }
      }
    }
  }

  .hobby-info {
    /*
      如果, 通过UI组件库设置的组件库自定义的 类名样式不生效, 可以使用 /deep/ 来深度设置
    */
    /deep/.van-grid-item__content {
      background-color: unset;
      color: #fff;

      .van-grid-item__text {
      color: #fff;
      }
    }

    /deep/[class*=van-hairline]::after {
      border: unset
    }

    /deep/.van-grid-item_content::after {
      border: unset;
    }
  }

  .gird-nav {
    .toutiao {
      font-size: 45px;
    }

    .toutiao-shoucang {
      color: #eb5253;
    }

    .toutiao-lishi {
      color: #ff9d1d
    }

    /deep/.van-gird-item__text {
      font-size: 30px;
    }
  }

  .logout-cell {
    text-align: center;
    color: red;
  }

</style>
